<template>
<div> 
    <div class = 'w-full m-auto rounded'>
    <div class = 'p-7 text-left bg-blue-600 text-white w-full'> 
         <h1 class = 'text-4xl'>Welcome, {{ this.$store.state.userData.username }}</h1>
         <p class = 'p-2 mt-5'>This is your homepage. It contains recent activity from other users and trending schools. Search specifics in the search bar below. </p>
             <Searchbar styling = "rounded-full" />
    </div>


     <div class = 'text-black bg-slate-200 p-2 m-auto w-full'>  
        <div class ='text-3xl text-black text-center md:text-left p-2 mx-4 mb-5'>Recent Activity</div>
        <div v-if = "getReviews.length === 0">No Reviews... Write your first review today!</div>
        <div class = 'flex flex-grid grid-cols-2 gap-2 overflow-x-scroll text-black md:p-2 md:m-2'> 
            <div class = 'm-auto md:m-2' v-for = "review in getReviews" :key = "review._id">
             
               <div style = "width:310px" :style = "'background:' + school(review.school_id).color" class = 'relative text-left p-3 bg-blue-500 text-white h-96 mr-auto md:mr-3'>
                    <h1 class = 'text-2xl'>{{ review.course }}</h1>
                    <h1 class = 'text-sm mb-1'>Someone from <a :href = "'/schools/view/' + review.school_id">{{ review.school }}</a></h1>
                    <h1 class = 'text-md text-gray-300'>{{ review.createdAt }}</h1>
                   <h1 class = 'my-1 mr-1 text-5xl text-yellow-500'>{{ rating(review.overall)}}</h1>
                   <p class = 'rounded p-1 mb-7'>{{ review.desc.slice(0,250)}} <br></p>
                     
                    <a :href = "'/reviews/' + review._id"><u class = 'absolute bottom-2 mx-1'>Learn More</u></a>
               </div>
           </div>   
        </div>
      
       </div>
    <div class = 'text-black bg-blue-100 p-2 m-auto w-full'>  
       <div class ='text-3xl text-black text-center md:text-left p-2 mx-2'>Trending Schools</div>
    <div class = 'flex flex-wrap p-2'>
       <div class = 'flex p-2' v-for = "school in schools.slice(0,4)" :key = "school._id">
            <a :href = "'/schools/view/' + school._id">
              <div class = 'p-4 w-72 h-72 flex flex-col' :style = "'background:' + school.color"> 
                 <img :src = "school.url" class = 'rounded-full w-32 h-32 m-auto mb-2'>
                <div class = 'mt-auto'>
                   <div class = 'text-white text-sm md:text-2xl text-left'>{{ school.name }}</div>
                   <div class = 'text-white text-sm text-left'>{{ school.city }}, {{ school.state }} | {{ school.count }} Reviews</div> 
                </div>
              </div>
            </a>
        </div>
      </div>
     </div>
</div>
</div>
</template>

<script>
import moment from "moment"; 
import Searchbar from "../components/Searchbar.vue"; 
export default {
    components:  {
       Searchbar
    },
    data() { 
        return { 
           reviews: [], 
           schools: "",
           topSchools: "",
           load: 8, 
           search: "", 
           results: []
        }
    }, 
    async mounted() { 
         let { data } = await this.$http.get('/reviews/all');
         
         for (let review of data) { 
             review.createdAt = moment(review.createdAt).fromNow()
         }
         this.reviews = data.reverse(); 

         let schools = await this.$http.get('/schools/all'); 

         for (let school of schools.data) { 
              school.count = (this.reviews.filter(review => { 
                  return review.school_id === school._id; 
              })).length; 
         }

        schools.data.sort((a, b) => { 
            return b.count - a.count; 
        })

         this.schools = schools.data; 
        
        
    }, 
    methods: { 
         rating(num) { 
              let rating = "";
              
              for (let i = 0; i < num; i++) { 
                   rating += "★"
              }
              for (let i = 0; i < (5 - num); i++) { 
                   rating += "☆"
              }

              return rating; 
         }, 
         school(id) { 
            let { schools } = this; 

             let school = schools.find(school => {
                 return id === school._id;
         })

             return school; 
         },
         dateString(date) { 
              return `${new Date(date).toLocaleDateString()}  `
         }, 
          filterReviews() { 
           let { reviews } = this; 
           let results = []; 
            for (let i = 0; i < reviews.length; i++) { 
                 let temp = reviews[i]; 

                 if (temp.course.toLowerCase().startsWith(`${this.search.toLowerCase().trim('')}`)) { 
                      results.push(temp); 
                 }
            }
            this.results = results; 
        }, 
    }, 
    computed: { 
        getReviews() { 
          return this.reviews.slice(0, this.load); 
        },
       
    }, 
 
}
</script>

<style>
span.text-7xl img {
    width: 80px; 
    height:80px; 
    margin: auto; 
}
div::-webkit-scrollbar-thumb {
  background: rgb(198, 193, 193)
}
</style>